.Checkout {
  align-items: center;
  text-align: center;
  margin-top: 56px;
  width: 100%;
  height: 100%;
}
.UserData {
  box-sizing: border-box;
  display: inline-block;
  padding: 4em;
  width: 80%;
  height: auto;
  background-color: #1c1c1c;
  color: white;
  box-shadow: 2px 2px 2px 2px #0009;
  margin-bottom: 5em;
}
.UserData input {
  width: 80%;
  background-color: #fff2;
  color: white;
  height: 1.5em;
  outline: none;
  border-radius: 0.3em;
  border: none;
  padding: 0.5em;
  margin: 0.5em;
}
.UserData label {
  text-align: left !important;
}
.SubmitButton {
  width: 10em !important;
  height: 4em !important;
}
.orderedburgers {
  display: flex;
  flex-flow: wrap;
}
.price {
  font-style: italic;
}
@media (min-width: 500px) {
  .UserData {
    width: 400px;
  }
}
@media (max-width: 499px) {
  .UserData {
    width: 100%;
  }
}
